/*ABOUT*/
.about-cards div img {
    max-height: 100px;
    min-height: 70px;
    min-width: 80px;
    object-fit: contain;
}

.about-cards div p {
    color: var(--light);
    line-height: 1.8;
    font-weight: 300;
    font-size: calc(1rem + 0.2vw);
    text-align: center;
}

.about-text {
    margin-bottom: calc(1rem + 2vw);
    display: flex;
    flex-direction: column;
    z-index: -1;
    align-items: center;
}

.about-text p {
    color: var(--light);
    font-weight: 600;
    text-align: center;
    font-size: calc(1rem + 1vw);
    margin-bottom: calc(1rem + 2vw);
}

.about-text .circle-gradient {
    padding-top: 10px;
    margin-top: -170px;
    z-index: -1;
    background: radial-gradient(12% 30% at 60% 45%, rgba(93, 38, 246, 0.349) 0%, rgba(92, 38, 246, 0) 100%);
    position: absolute;
    width: 100%;
    height: 10%;
}

.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: calc(1rem + 3vw);
}

.about-heading {
    margin-top: 150px;
}

.about h2 {
    font-size: calc(1rem + 1.5vw);
    font-weight: 900;
    line-height: 0.5;
}

.about-cards h4 {
    color: var(--light);
    font-size: calc(1rem + 0.6vw);
    font-weight: 900;
    line-height: 0;
    margin-bottom: calc(1rem + 0.6vw);
}

.about-cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/*  ABOUT CARD TILT ANIMATION   */

.about-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #090909;
    width: calc(1rem + 16vw);
    padding: calc(1rem + 1.6vw);
    margin: calc(1rem + 0vw);
    box-shadow: 2px 4px 12px #5d26f60c;
    border-radius: 20px;
    transition: all 0.6s ease-out;
    transform: rotateX(0deg) rotateY(0deg);
    perspective: 1000px;
    transform-style: preserve-3d;
}

.about-cards .about-card-wrapper {
    transition: all 0.6s ease-out;
    perspective: 1000px;
}

.about-cards .about-card-wrapper:hover {
    transform: scale(1.1);
}

.about-card:hover {
    
}

.t_over {
    width: 33.333%;
    height: 33.333%;
    position: absolute;
    z-index: 1;
}

.t_over:nth-child(1) {
    left: 0;
    top: 0;
}

.t_over:nth-child(2) {
    left: 33.333%;
    top: 0;
}

.t_over:nth-child(3) {
    left: 66.666%;
    top: 0;
}

.t_over:nth-child(4) {
    left: 0;
    top: 33.333%;
}

.t_over:nth-child(5) {
    left: 33.333%;
    top: 33.333%;
}

.t_over:nth-child(6) {
    left: 66.666%;
    top: 33.333%;
}

.t_over:nth-child(7) {
    left: 0;
    top: 66.666%;
}

.t_over:nth-child(8) {
    left: 33.333%;
    top: 66.666%;
}

.t_over:nth-child(9) {
    left: 66.666%;
    top: 66.666%;
}

.t_over:nth-child(1):hover~.about-card {
    box-shadow: 3px 10px 20px #5d26f696;
    transform: rotateX(-20deg) rotateY(20deg);
}

.t_over:nth-child(2):hover~.about-card {
    box-shadow: 3px 10px 20px #5d26f6cc;
    transform: rotateX(-20deg) rotateY(0deg)
}

.t_over:nth-child(3):hover~.about-card {
    box-shadow: 3px 2px 20px #5d26f6cc;
    transform: rotateX(-20deg) rotateY(-20deg)
}

.t_over:nth-child(4):hover~.about-card {
    box-shadow: 3px 10px 20px #5d26f6cc;
    transform: rotateX(0deg) rotateY(20deg)
}

.t_over:nth-child(5):hover~.about-card {
    box-shadow: 3px 10px 20px #5d26f691;
    transform: rotateX(0deg) rotateY(0deg)
}

.t_over:nth-child(6):hover~.about-card {
    box-shadow: 3px 10px 20px #5d26f686;
    transform: rotateX(0deg) rotateY(-20deg)
}

.t_over:nth-child(7):hover~.about-card {
    box-shadow: 0px -10px 20px #5d26f66e;
    transform: rotateX(20deg) rotateY(20deg);
}

.t_over:nth-child(8):hover~.about-card {
    box-shadow: 3px -10px 20px #5d26f66e;
    transform: rotateX(20deg) rotateY(0deg)
}

.t_over:nth-child(9):hover~.about-card {
    box-shadow: 3px -10px 20px #5d26f657;
    transform: rotateX(20deg) rotateY(-20deg)
}