/* 

Primary - #7A5B0A
Primary Light - #0C0C0C
Primary Lighter - #FFD977
Secondary - #000000
Secondary Light - #B2B2B2
Secondary Lighter - #1F1F1F
middle: #7C7C7C;
darkText: #545454;
Lighter - #FAFAFA
Light: #F8F8F8;
white: #ffffff;
*/


:root {
  --primary: rgb(92, 38, 246);
  --primaryLight: rgba(93, 38, 246, 0.5);
  --primaryLighter: rgba(93, 38, 246, 0.25);
  --secondary: #0E0E0E;
  --secondaryLight: #1f1f1f;
  --secondaryLighter: #282828;
  --middle: #7A7A7A;
  --darkText: #545454;
  --light: #F8F8F8;
  --lighter: #FAFAFA;
  --white: #ffffff;
}

html {
  background: var(--secondary);
}

a {
  text-decoration: none;
}

p {
  color: var(--darkText);
}

h3 {
  color: var(--primary);
  font-weight: 200;
}

.flex {
  margin: auto;
  display: flex;
}


.btn {
  text-align: center;
  display: block;
  color: var(--lighter);
  background-color: var(--primary);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.342);
  transition: all 0.3s ease 0s;
  cursor: pointer;
}

.btn:hover{
  color: var(--primary);
  background-color: var(--primaryLighter);
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.2);
}

.pg-link {
  display: flex;
  align-items: center;
  font-weight: 400;
  color: var(--primary);
  font-size: calc(1rem + 0.4vw);
  gap: calc(1rem + 0.2vw);
  text-align: center;
}

.pg-link img {
  width: calc(1rem + 12vw);
}

.pg-link:hover {
  color: var(--primaryLighter);
}

.App {
  background: var(--secondary);
  color: var(--light);
  position: absolute;
  width: 100vw;
  z-index: -1;
  font-family: 'Poppins', sans-serif;
}

.app-container:nth-child(1){
  visibility: hidden;
}

.app-container > * {
  visibility: visible;
}

header.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
  padding: 4vh 4vw 1.5vh 4vw;
  margin-top: -20px;
}

.logo {
  width: calc(1rem + 3vw);
  height: auto;
}

ul.menu {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
}

.menu li {
  padding-left: 90px;
}

.menu li a {
  color: var(--lighter);
  text-decoration: none;
  font-family: 'Poppins';
  font-weight: 600;
  font-size: calc(1rem + 0.2vw);
}

.menu li a:hover {
  color: var(--primaryLighter);
}

/* HERO */

.hero {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.hero-text {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15% 4%;
  width: 100%;
}

.hero-h1 {
  color: var(--light);
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-text h1 {
  font-family: 'Poppins';
  font-weight: 900;
  font-size: calc(1rem + 4vw);
  line-height: 60px;
  margin: 0;
  padding-bottom: 30px;
}

.emph {
  color: var(--primary);
  display: inline;
}

.hero-text p {
  font-weight: 500;
  line-height: 0;
  font-size: calc(1rem + 0.2vw);
  color: var(--middle);
  text-align: center;
  margin-bottom: calc(1rem + 3vh);
}

.hero .btn {
  width: calc(1rem + 17vw);
  font-weight: 600;
  font-size: calc(1rem + 0.3vw);
  margin: 25px;
  border-radius: 5px;
  padding: 15px 20px;
}

img.globe {
  position: absolute;
  z-index: -1;
  height: calc(1rem + 70vh);
  top: calc(1rem + 12vh);
  right: calc(1rem + 10vw);
}


/* PHONE scroll */

.scene {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative!important;
}

.phones {
  width: auto;
  height: auto;
  max-width: 396px;
  min-height: 701px;
  max-height: 701px;
  object-fit: cover;
  object-position: 0 50%;
}

/* Services */

.services {
  display: flex;
  margin: auto;
}

.services-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.services-text {
  padding: 0 calc(1rem + 0.9vw) 0 calc(1rem + 4vw);
}

.services-text p {
  color: var(--light);
  font-size: calc(1rem + 1.8vw);
  line-height: 1.1;
  font-weight: 500;
}


.services .circle-gradient {
  background: radial-gradient(16% 40% at 35% 50%, rgba(93, 38, 246, 0.5) 0%, rgba(92,38,246,0) 100%);
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  }
  


/* FOOTER */

.footer {
  display: flex;
  flex-direction: column;
  margin-top: calc(1rem + 5vw);
  padding-top: calc(1rem + 2vw);
  border-top: 0.1px var(--light) solid;
}

.footer-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 5px;
  align-items: center;
  margin: auto;
}

.footer a{
  color: var(--light);
}

.footer a:hover {
  color: var(--primary);
}

.footer-links {
  display: flex;
  justify-content: center;
  padding-top: 50px;
  gap: 40px;
}

.footer-links a {
  font-weight: 500;
}

.footer span {
  margin:auto;
  padding-top: 50px;
  color: var(--light);
}

/* CONTACT FORM */ 

form.contact-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: calc(1rem + 2vh);
}

input, textarea{
  background-color: var(--secondaryLight);
  padding-block: 20px;
  padding-inline: 20px;
  width: calc(1rem + 35vw);
  border-radius: 0;
  font-family: 'Poppins', sans-serif;
  border: none;
}

input[type=text]:focus, textarea:focus {
  border: solid 1px var(--primary);   
}

.contact-form button.btn {
  margin-top: 30px;
  padding: 25px;
  border: none;
  width: calc(1rem + 18vw);
  font-size: calc(1rem + 0.15vw);
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  color: white;  
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.342);
  cursor: pointer;
  border-radius: 20px;
}

button.btn:hover {
  color: var(--primary);
}


/*PAGES*/


.pg-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: calc(1rem + 0.5vw);
  margin-left: calc(1rem + 1.5vw);
  position: absolute;
}

.pg-title h1 {
  transform: rotate(90deg);
  width: fit-content;
  color: var(--light);
  font-weight: 300;
  font-size: calc(1rem + 0.9vw);
}

.pg-title hr {
  border-color: var(--primary);
  background-color: var(--primary);
  width: 0.5px;
  height: 8vw; 
  display: inline-block;
  margin-bottom: 1vh;
  margin-top: 1vh;
}

.pg-title hr:nth-child(1) {
  height: 3vw;
}

.contact-container {
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin: auto;
  margin-top: 6vh;
  font-size: calc(1rem + 0.3vw);
}

.contact-container h3 {
  line-height: 0;
  color: var(--primary);
}

.contact-container h3 a {
  color: var(--light)
}

.contact-container h3 a:hover {
  color: var(--light);
}